@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.card {
    border-radius: 5px;
    box-shadow: 20px 1px 8px rgba(1,2,44,0.25);
}
.exercise-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #4b4b4b;
  }
  
  .exercise-item__type {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 1rem;
    gap: 1rem;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-flex-flow: row-reverse;
            flex-flow: row-reverse;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex: 1 1;
            flex: 1 1;
  }
  
  .exercise-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0 1rem;
    color: white;
  }
  
  .exercise-item__duration {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  @media (min-width: 580px) {
    .exercise-item__type {
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
      -webkit-flex: 1 1;
              flex: 1 1;
    }
  
    .exercise-item__type h2 {
      font-size: 1.25rem;
    }
  
    .exercise-item__duration {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
.exercise-date {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .exercise-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .exercise-date__year {
    font-size: 0.75rem;
  }
  
  .exercise-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }
.exercises {
    padding: 1rem;
    background-color: rgb(55, 99, 8);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
